// Timer.js v3.4.0

(function() {
	//---------------------------------------------------------------------------
	// Timer  (/)
	//---------------------------------------------------------------------------
	var timerInterval = 100; /*  */

	ui.timer = {
		/*  */
		TID: null /* ID */,
		current: 0 /* getTime()() */,

		/*  */
		bseconds: 0 /* () */,
		timerEL: null /*  */,

		/*  */
		worstACtime: 0 /* () */,
		nextACtime: 0 /*  */,

		//---------------------------------------------------------------------------
		// tm.reset()      0
		// tm.start()      update()200ms
		// tm.update()     200ms
		//---------------------------------------------------------------------------
		init: function() {
			this.worstACtime = 0;
			this.timerEL = document.getElementById("timerpanel");
			this.showtime(0);
		},
		start: function() {
			var self = this;
			if (!!this.TID) {
				return;
			}
			ui.puzzle.resetTime();
			this.update();
			this.TID = setInterval(function() {
				self.update();
			}, timerInterval);
		},
		stop: function() {
			if (!this.TID) {
				return;
			}
			clearInterval(this.TID);
			this.TID = null;
		},
		update: function() {
			this.current = pzpr.util.currentTime();

			if (ui.puzzle.playeronly) {
				this.updatetime();
			}
			if (ui.menuconfig.get("autocheck_once")) {
				var mode = ui.menuconfig.get("autocheck_mode");
				this.autocheck(mode === "guarded");
			}
		},

		//---------------------------------------------------------------------------
		// tm.updatetime() 
		// tm.label()      
		//---------------------------------------------------------------------------
		showtime: function(seconds) {
			var hours = (seconds / 3600) | 0;
			var minutes = ((seconds / 60) | 0) - hours * 60;
			seconds = seconds - minutes * 60 - hours * 3600;

			if (minutes < 10) {
				minutes = "0" + minutes;
			}
			if (seconds < 10) {
				seconds = "0" + seconds;
			}

			this.timerEL.innerHTML = [
				this.label(),
				!!hours ? hours + ":" : "",
				minutes,
				":",
				seconds
			].join("");
		},
		updatetime: function() {
			var seconds = (ui.puzzle.getTime() / 1000) | 0;
			if (this.bseconds === seconds) {
				return;
			}
			this.showtime(seconds);
			this.bseconds = seconds;
		},
		label: function() {
			return ui.selectStr("", "Time: ");
		},

		//---------------------------------------------------------------------------
		// tm.autocheck()    
		//---------------------------------------------------------------------------
		autocheck: function(guarded) {
			var puzzle = ui.puzzle;
			if (
				this.current > this.nextACtime &&
				puzzle.playmode &&
				!puzzle.checker.inCheck &&
				puzzle.board.trialstage === 0 &&
				!puzzle.getConfig("variant")
			) {
				var check = puzzle.check(false);
				if (check.complete && (!guarded || !check.undecided)) {
					ui.timer.stop();
					puzzle.mouse.mousereset();
					ui.menuconfig.set("autocheck_once", false);
					if (ui.callbackComplete) {
						ui.callbackComplete(puzzle, check);
					}
					ui.notify.alert("", "Complete!");
					return;
				}

				this.worstACtime = Math.max(
					this.worstACtime,
					pzpr.util.currentTime() - this.current
				);
				this.nextACtime =
					this.current +
					(this.worstACtime < 250
						? this.worstACtime * 4 + 120
						: this.worstACtime * 2 + 620);
			}
		}
	};

	//---------------------------------------------------------------------------
	// UndoTimer   Undo/Redo
	//---------------------------------------------------------------------------
	var undoTimerInterval = 25 /*  */,
		execWaitTime = 300; /* 1wait */

	ui.undotimer = {
		/*  */
		TID: null /* ID */,

		inUNDO: false /* Undo */,
		inREDO: false /* Redo */,

		//---------------------------------------------------------------------------
		// ut.reset()  
		//---------------------------------------------------------------------------
		reset: function() {
			this.stop();
		},

		//---------------------------------------------------------------------------
		// ut.startUndo() Undo
		// ut.startRedo() Redo
		// ut.stopUndo() Undo
		// ut.stopRedo() Redo
		//---------------------------------------------------------------------------
		startUndo: function() {
			if (!(this.inUNDO || this.inREDO)) {
				this.inUNDO = true;
				this.proc();
			}
		},
		startRedo: function() {
			if (!(this.inREDO || this.inUNDO)) {
				this.inREDO = true;
				this.proc();
			}
		},
		stopUndo: function() {
			if (this.inUNDO) {
				this.inUNDO = false;
				this.proc();
			}
		},
		stopRedo: function() {
			if (this.inREDO) {
				this.inREDO = false;
				this.proc();
			}
		},

		//---------------------------------------------------------------------------
		// ut.start() Undo/Redo
		// ut.stop()  Undo/Redo
		//---------------------------------------------------------------------------
		start: function() {
			var self = this;
			function handler() {
				self.proc();
			}
			function inithandler() {
				clearInterval(self.TID);
				self.TID = setInterval(handler, undoTimerInterval);
			}
			this.TID = setInterval(inithandler, execWaitTime);
			this.exec();
		},
		stop: function() {
			this.inUNDO = false;
			this.inREDO = false;

			clearInterval(this.TID);
			this.TID = null;
		},

		//---------------------------------------------------------------------------
		// ut.proc()  Undo/Redo
		// ut.exec()  Undo/Redo
		//---------------------------------------------------------------------------
		proc: function() {
			if ((this.inUNDO || this.inREDO) && !this.TID) {
				this.start();
			} else if (!(this.inUNDO || this.inREDO) && !!this.TID) {
				this.stop();
			} else if (!!this.TID) {
				this.exec();
			}
		},
		exec: function() {
			if (!this.checknextprop()) {
				this.stop();
			} else if (this.inUNDO) {
				ui.puzzle.undo();
			} else if (this.inREDO) {
				ui.puzzle.redo();
			}
		},

		//---------------------------------------------------------------------------
		// ut.checknextprop()  Undo/Redo
		//---------------------------------------------------------------------------
		checknextprop: function() {
			var opemgr = ui.puzzle.opemgr;
			var isenable =
				(this.inUNDO && opemgr.enableUndo) ||
				(this.inREDO && opemgr.enableRedo);
			return isenable;
		}
	};
})();
